import React, { Component, lazy } from 'react'
import { Redirect } from 'react-router';
import {
  CBadge,
  CDataTable,
  CFormGroup,
  CLabel,
  CInput,
  CButton,
  CForm,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CInputCheckbox,
  CCol,
  CRow,
} from '@coreui/react'
import {fieldtabelrealisasi} from './Sourcerealisasi.js'
import Tambahrealisasi from './Tambahrealisasi.js'

class Rekamrealisasi extends Component {
  constructor(props) {
    super(props);
    this.state = {
  }
}
  render(){
      const Getbadge = status => {
        switch (status) {
          case 'Draft': return 'success'
          case 'Terkirim': return 'info'
        }
      }  
    return(
      <CCard>
        <CCardHeader className="bg-light text-dark" >
          <h5>Form Rekam Realisasi Impor SKBPPN</h5>
        </CCardHeader>      
        <CCardBody>
          <div className="p-2 d-flex justify-content-end">
          <CRow>
            <Tambahrealisasi/>
          </CRow>
          </div>            
          <CRow>
            <CDataTable
            itemsPerPage={3}
            fields={fieldtabelrealisasi}
            pagination
            scopedSlots = {
                {
                'status':
                  (item)=>(
                    <td>
                      <CBadge color={Getbadge(item.status)}>
                        {item.status}
                      </CBadge>
                    </td>
                  ),
                'action' :
                (item,index) => {
                return (
                    <td>
                    <CButton size="sm" color="success" >Edit</CButton>{' '}
                    <CButton size="sm" color="info" >Lihat</CButton>{' '}
                    <CButton size="sm" color="danger" >Hapus</CButton>
                    </td>
                    )
                },  
                }
            }
            />
          </CRow>                   
        </CCardBody>
      </CCard>
      )
  }  
}

export default Rekamrealisasi
