import React, { useState } from 'react'
import Select from 'react-select';
import {
  CButton,
  CCard,
  CCardBody,
  CFormGroup,
  CSelect,
  CTabs,
  CNav,
  CNavItem,
  CNavLink,
  CTabContent,
  CTabPane,
  CLabel,
  CTextarea,
  CInput,
  CCardHeader,
  CDataTable,
  CCol,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CRow
} from '@coreui/react'


const Tambahrealisasi = (props) => {

  const [modal, setModal] = useState(false)
//   const [value, setValue] = useState({uraian_btki:''})
//   const [no, setNo] = useState(1)
  
//   const handleReactSelect = (selectedOption, { name }) => {
//     setValue(prevState => ({...prevState, no: no, idhost: props.idhost, [name] : selectedOption.value, uraian_btki : selectedOption.label  }));
//   }
//   const handleClick = () => {
//       props.calleback(value)
//       setNo(no + 1)
//       setModal(false)
//       setValue({uraian_btki:''})
//   }
//   console.log('ÃœJI COBA TAMBAH BARANG', value)
  return (
    <>
      <CButton 
        onClick={() => {
            setModal(!modal) 
        }}
        className="mr-1"
        color="info"
        size="sm"
      >
        <a>Tambah</a>
      </CButton>
      <CModal 
        show={modal} 
        onClose={setModal}
        size="lg"
        closeOnBackdrop={false}
      >
        <CModalHeader closeButton>
          <CModalTitle>Tambah Realisasi</CModalTitle>
        </CModalHeader>
        <CModalBody>
            <CRow>
                <CCol xs="4">
                <CFormGroup>
                    <CLabel htmlFor="npwp">NPWP</CLabel>
                    <CInput name="npwp" placeholder="Masukkan NPWP" />
                </CFormGroup>
                </CCol>
                <CCol xs="4">
                <CFormGroup>
                    <CLabel htmlFor="nm_perusahaan">Nama Perusahaan</CLabel>
                    <CInput name="tglmaster" placeholder="Masukkan Nama Perusahaan" />
                </CFormGroup>
                </CCol>
                <CCol xs="4">
                <CFormGroup>
                    <CLabel htmlFor="tglmaster">Nomor Faktur</CLabel>
                    <CInput name="no_faktur" placeholder="Masukkan Nomor Faktur" />
                </CFormGroup>
                </CCol>                
            </CRow> 
            <CRow>
                <CCol xs="3">
                <CFormGroup>
                    <CLabel htmlFor="npwp">Kode HS</CLabel>
                    <Select/>
                </CFormGroup>
                </CCol>
                <CCol xs="9">
                <CFormGroup>
                    <CLabel htmlFor="nm_perusahaan">Jenis Barang</CLabel>
                    <CInput name="tglmaster" placeholder="Masukkan Nama Perusahaan" />
                </CFormGroup>
                </CCol>               
            </CRow>
            <CRow>
                <CCol xs="12">
                <CFormGroup>
                    <CLabel htmlFor="npwp">Spesifikasi Teknis</CLabel>
                    <CTextarea/>
                </CFormGroup>
                </CCol>               
            </CRow>
            <CRow>
                <CCol xs="3">
                <CFormGroup>
                    <CLabel htmlFor="npwp">Kuantitas</CLabel>
                    <CInput name="npwp" placeholder="Masukkan NPWP" />
                </CFormGroup>
                </CCol>
                <CCol xs="3">
                <CFormGroup>
                    <CLabel htmlFor="nm_perusahaan">Harga Jual Satuan</CLabel>
                    <CInput name="tglmaster" placeholder="Masukkan Nama Perusahaan" />
                </CFormGroup>
                </CCol>
                <CCol xs="3">
                <CFormGroup>
                    <CLabel htmlFor="tglmaster">Harga Total</CLabel>
                    <CInput name="no_faktur" placeholder="Masukkan Nomor Faktur" />
                </CFormGroup>
                </CCol> 
                <CCol xs="3">
                <CFormGroup>
                    <CLabel htmlFor="tglmaster">Nilai PPN</CLabel>
                    <CInput name="no_faktur" placeholder="Masukkan Nomor Faktur" />
                </CFormGroup>
                </CCol>                                
            </CRow> 
            <CRow>
                <CCol xs="12">
                <CFormGroup>
                    <CLabel htmlFor="npwp">Keterangan</CLabel>
                    <CTextarea
                    rows={4}/>
                </CFormGroup>
                </CCol>               
            </CRow>                                                                                                                    
        </CModalBody>
        <CModalFooter>
          <CButton 
          onClick={() => setModal(false)}
          color="primary">Tambah</CButton>{' '}
          <CButton 
            color="secondary" 
            onClick={() => setModal(false)}
          >
            Cancel
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  )
}    

export default Tambahrealisasi